.card {
  box-sizing: border-box;
  width: 200px;
  height: 407px;
  display: flex;
  flex-direction: column;
  background: $color-light;
  border: 1px solid #e5e5e5;
  border-radius: 5px;
  transition: transform  $time-transformation;

  &__images {
    width: 160px;
    height: 134px;
    padding: 32px 19px 36px;
  }

  &__about {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $color-dark;
    padding: 0 16px 3.5px;
    margin: 0;
  }

  &__code {
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: #616070;
    padding-left: 16px;
    padding-bottom: 7.5px;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    padding: 0 16px;
    align-items: center;
  }

  &__reviews {
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    display: flex;
    align-items: flex-end;
    text-align: right;
    color: $color-dark;
    flex: none;
    order: 1;
    flex-grow: 0;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    flex: none;
    align-items: flex-end;
    padding: 15px 16px 15.5px;
  }

  &__title {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: #616070;
    order: 0;
    margin: 0;
    transition: color $time-transformation;
  }

  &__value {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    text-align: right;
    color: $color-dark;
    order: 1;
    margin: 0;
  }

  &__button {
    margin: 0 auto;
    padding-bottom: 16.5px;
  }

  &__link {
    width: 166px;
    height: 40px;
    bottom: 17px;
    background: $color-blue;
    border-radius: 5px;
    border: $color-blue;
    text-decoration: none;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    text-transform: uppercase;
    color: $color-light;
    transition: color $time-transformation;
  }

  &__link:hover {
    background-color: $color-light;
    border: 1px solid $color-blue;
    color: $color-blue;
  }

  &:hover {
    transform: scale(1.2);

    .card__about {
      color: #34568b;
    }

    .card__link {
      color: $color-blue;
    }
  }

}
